import React, { useEffect, useState } from 'react';
import {} from 'react-dom';
import { useParams } from 'react-router-dom';
import { useHistory } from 'react-router-dom/cjs/react-router-dom.min';
import { Nav } from 'react-bootstrap';
import { CSSTransition } from 'react-transition-group';
import { connect } from 'react-redux';
import Cartdata from './cartdata.js';

function Detail(props) {
    let [alert, setAlert] = useState(true);
    let [tab, setTab] = useState(0);
    let [event, setEvent] = useState(false);

    useEffect(()=>{
        let timer = setTimeout(()=>{
            setAlert(false);
        }, 2000);
        return (()=>{ clearTimeout(timer); })
    },[alert]);

    useEffect(()=>{
        var arr = localStorage.getItem('watched');
        if (arr == null) {
            arr = [];
        } else {
            arr = JSON.parse(arr);
        }

        arr.push(--id);
        arr = new Set(arr);
        arr = [...arr];
        
        
        
        localStorage.setItem('watched', JSON.stringify(arr));
    },[])



    let history = useHistory();
    let { id } = useParams();

    let product = props.shoes.find((x)=>{return x.id == id});
    return (
        <div className="container">
            <Alert setAlert={setAlert} alert={alert}></Alert>
            <div className="row">
                <div className="col-md-6">
                    <img src={`https://codingapple1.github.io/shop/shoes${++id}.jpg`} width="100%" />
                </div>
                <div className="col-md-6 mt-4">
                    <h4 className="pt-5">{product.title}</h4>
                    <p>{product.content}</p>
                    <p>{product.price}</p>
                    <button className="btn btn-danger" onClick={()=>{
                        props.dispatch({ type : '주문하기', 데이터 : Cartdata[--id] })
                        history.push('/cart');
                    }}>주문하기</button> 
                    <button className="btn btn-danger" onClick={()=>{
                        history.goBack();
                    }}>뒤로가기</button> 
                </div>
            </div>
            <Nav className="mt-5" variant="pills" defaultActiveKey="link-0">
                <Nav.Item>
                    <Nav.Link eventKey="link-0" onClick={()=>{ setEvent(false);  setTab(0);
                    }}>첫번째</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-1" onClick={()=>{ setTab(1); setEvent(false);
                    }}>두번째</Nav.Link>
                </Nav.Item>
                <Nav.Item>
                    <Nav.Link eventKey="link-2" onClick={()=>{ setTab(2); setEvent(false);
                    }}>세번째</Nav.Link>
                </Nav.Item>
            </Nav>
            <CSSTransition in={event} classNames="fade" timeout={500}>   
                <TabContent tab={tab} setEvent={setEvent}/>
            </CSSTransition> 
        </div>
          
    )
}

function Alert(props) {

    useEffect(()=>{
        let timer = setTimeout(()=>{
            props.setAlert(false);
        }, 2000);
        return (()=>{ clearTimeout(timer); })
    },[alert]);


    if (props.alert === true) {
        return <div className="alert">
                    <p>현재 20% 할인 진행 중 입니다.</p>
                </div>  
    }
        return null;
}


function TabContent(props) {
    useEffect(()=>{
        props.setEvent(true);
    })
    if (props.tab === 0) {
        return <div>첫번째 내용</div>
    } else if (props.tab === 1) {
        return <div>두번째 내용</div>
    } else {
        return <div>세번째 내용</div>
    }
}

function cartReducer(state) {
    return {
        state : state.reducer
    }
}

export default connect(cartReducer)(Detail);